import { console_log, isArrayCheck, Log } from "../../../utils/helper";
import request from "../../../utils/request";
import makeToast from "../../../utils/Toaster";
import {
  CALL_DATA,
  CALL_STATUS,
  GET_ALL_USERS,
  GET_MY_APOINTMENTS,
  GET_MY_PROFILE,
} from "../Action.Constant";
import { isLoading } from "../Loader";

export const createUser =
  (userData, close, refreshState) => async (dispatch) => {
    console_log("createRoom", userData);
    dispatch(isLoading(true));
    try {
      const res = await request.post("/users/add", userData);
      const { message, status, data } = res.data;
      if (!status === "Success") throw res.data;
      if (status === "Success") {
        makeToast("success", message);
        console_log("Data", data);
        dispatch(getAllUsers());
        close();
        refreshState();
        dispatch(isLoading(false));
      }
    } catch (e) {
      makeToast("error", e.message);
      console_log("createUser error", e);
      dispatch(isLoading(false));

      // makeToast("createUser error", e.message);
    }
  };

export const updateUser =
  (userData, close, message = null) =>
    async (dispatch) => {
      console_log("updateUser", userData);
      dispatch(isLoading(true));

      try {
        const res = await request.post("/users/update", userData);
        const { message, status, data } = res.data;
        if (!status === "Success") throw res.data;
        if (status === "Success") {
          makeToast("success", message ? message : "User Updated Successfully");
          console_log("Data", data);
          close();
          dispatch(getAllUsers());
          dispatch(isLoading(false));
        }
      } catch (e) {
        console_log("updateUser error", e);
        dispatch(isLoading(false));

        // makeToast("createUser error", e.message);
      }
    };

export const deleteUser =
  (userId, close) => async (dispatch) => {
    console_log("delete ==>", userId);
    try {
      const res = await request.delete(`/users/delete/${userId}`);
      const { message, status, data } = res.data;
      if (!status === "Success") throw res.data;
      if (status === "Success") {
        makeToast("success", message);
        console_log("Data", data);
        dispatch(getAllUsers());
        close();
      }
    } catch (e) {
      console_log("delete user error", e);
      // makeToast("createUserModal error", e.message);
    }
  };

export const updateUserActiveStatus =
  (userId, isActive, close) => async (dispatch) => {
    console_log("path ==>", userId);
    try {
      const res = await request.patch(`/users/updateActiveStatus/${userId}`, { isActive });
      const { message, status, data } = res.data;
      if (!status === "Success") throw res.data;
      if (status === "Success") {
        makeToast("success", message);
        console_log("Data", data);
        dispatch(getAllUsers());
        close();
      }
    } catch (e) {
      console_log("updateUser status Modal error", e);

      // makeToast("createUserModal error", e.message);
    }
  };

export const getAllUsers = () => async (dispatch) => {
  console_log("Call => getAllUsers");
  try {
    const res = await request.get("/users/all");
    const { message, status, data } = res.data;
    if (!status === "Success") throw res.data;
    if (status === "Success") {
      console_log("Data", data?.data);
      dispatch({
        type: GET_ALL_USERS,
        payload: data?.response,
      });
    }
  } catch (e) {
    console_log("getAllUsers error", e);

    // makeToast("getAllUserListing error", e.message);
  }
};
